import { Directive, TemplateRef, Optional } from '@angular/core';

/**
 * A directive that selects a [template](https://angular.io/docs/ts/latest/guide/template-syntax.html)
 * within the `<kendo-chart-tooltip>` component to be used for the
 * [series tooltip]({% slug tooltips_chart_charts_kendouiforangular %}#series-tooltip).
 *
 * @example
 * ```ts
 * import { Component } from '@angular/core';
 *
 * @@Component({
 *   selector: 'my-app',
 *   template: `
 *     <kendo-chart [categoryAxis]="categoryAxes">
 *       <kendo-chart-tooltip>
 *          <template kendoSeriesTooltipTemplate let-value="value">
 *             Value is {{value}}
 *           </template>
 *       </kendo-chart-tooltip>
 *       <kendo-chart-series>
 *         <kendo-chart-series-item [data]="[1, 2, 3]">
 *         </kendo-chart-series-item>
 *       </kendo-chart-series>
 *     </kendo-chart>
 *   `
 * })
 * class AppComponent {
 *   private legendVisible: boolean = true;
 *   private seriesVisible: boolean = true;
 *
 *   public toggleSeries(): void {
 *     this.seriesVisible = !this.seriesVisible;
 *   }
 *
 *   public toggleLegend(): void {
 *     this.legendVisible = !this.legendVisible;
 *   }
 * }
 *
 * ```
 */
@Directive({
    selector: '[kendoSeriesTooltipTemplate]'
})
export class SeriesTooltipTemplateDirective {
  constructor(@Optional() public templateRef: TemplateRef<any>) {}
}



